.topbarContainer{
  height:50px;
  width: 100%;
  background-color: #1877f2;
  display: flex;
  align-items: center;
  position: sticky;
  top:0
}

.topbarLeft{
  flex:3;
}

.topbarCenter{
  flex:5;
}
.topbarRight{
  flex:4;
  display: flex;
  align-items: center;
  justify-content: space-around;
  color:white;
}
.topbarLink{
  margin-top: 10px;
  font-size: 14px;
  cursor:pointer;
}
.topbarIcons{
  display: flex;
}

.topbarIconItem{
  margin-right: 15px;
  cursor: pointer;
  position: relative;
}

.topbarIconBadge{
  width: 15px;
  height: 15px;
  background-color: red;
  border-radius: 50%;
  color:white;
  position: absolute;
  top:-5px;
  right:-5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
}

.searchbar{
  width:100%;
  height:30px;
  background-color: white;
  border-radius: 30px;
  display: flex;
  align-items: center;

}
.searchIcon{
  margin-left: 10px !important;
  font-size: 20px !important;
}

.searchInput{
  width: 70%;
  border:none;
}
.searchInput:focus{
  outline: none;
}

.topbarImg{
  width: 32px;
  height:32px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}

.logo{
  font-size: 24px;
  margin-left: 20px;
  font-weight: bold;
  color:white;
  cursor:pointer;
}
